import myFetch from '@/utils/myFetch'

export function queryDrawByCondition(pPageIndex,pPageSize,pTypeCode,pColor){
	var param=JSON.stringify({
		'pageIndex':pPageIndex,
		'pageSize':pPageSize,
		'type':pTypeCode,
		'color':pColor
	});
	return myFetch({
		method:'post',
		url:'/microServiceRender/queryPaginationDraw.do',
		data:param
	})	
}
export function updateDrawByID(pID,pDrawName){
	var param=JSON.stringify({
		'id':pID,
		'drawName':pDrawName
	});
	return myFetch({
		method:'post',
		url:'/microServiceRender/updateDrawArgumentsById.do',
		data:param
	})	
}
export function updateTypeLabelByID(pID,pTypeLabel){
	var param=JSON.stringify({
		'id':pID,
		'typeLabel':pTypeLabel
	});
	return myFetch({
		method:'post',
		url:'/microServiceRender/updateTypeLabelById.do',
		data:param
	})	
}
export function addDraw(pID,pDrawName,pTypeLabel){
	var param=JSON.stringify({
		'id':pID,
		'drawName':pDrawName,
		'typeLabel':pTypeLabel
	});
	return myFetch({
		method:'post',
		url:'/microServiceRender/addDrawArguments.do',
		data:param
	})	
}


