export function getTypesById(pId){
	var param=JSON.stringify({
		'id':pId
	});
	return  myFetch({
		method:'post',
		url:'/microServiceType/queryChildrenTypeByTypeId.do',
		data:param
	});
}
export function getTypes(){
	var param=JSON.stringify({});
	return  myFetch({
		method:'post',
		url:'/microServiceType/listType.do',
		data:param
	});
}
export function getPaginationTypes(pPageIndex,pPageSize,pTypeCode,pTypeName){
	var param=JSON.stringify({
		'pageNum':pPageIndex,
		'pageSize':pPageSize,
		'typeCode':pTypeCode,
		'typeName':pTypeName
	});
	return  myFetch({
		method:'post',
		url:'/microServiceType/queryTypeByCondition.do',
		data:param
	});
}
export function updateBatchType(pId,pTypeOld,pTypeNew){
	var param=JSON.stringify({
		'id':pId,
		'typeOld':pTypeOld,
		'typeNew':pTypeNew
	});
	return myFetch({
		method:'post',
		url:'/microServiceType/updateBatchType.do',
		data:param
	});
}
export function updateType(pParentId,pTypeId,pTypeName,pOriginParentId){
	var param=JSON.stringify({
		'parentId':pParentId,
		'typeId':pTypeId,
		'typeName':pTypeName,
		'originParentId':pOriginParentId
	});
	return myFetch({
		method:'post',
		url:'/microServiceType/updateType.do',
		data:param
	});
}
export function addType(pParentId,pTypeName,pTypeCode){
	var param=JSON.stringify({
		'parentId':pParentId,
		'typeName':pTypeName,
		'typeCode':pTypeCode
	});
	return myFetch({
		method:'post',
		url:'/microServiceType/addType.do',
		data:param
	});
}
export function deleteType(pTypeId){
	var param=JSON.stringify({
		'typeId':pTypeId
	});
	return myFetch({
		method:'post',
		url:'/microServiceType/deleteType.do',
		data:param
	});
}



